<template>
    <section class="main">
        <loader v-if="!configLoaded" />
        <h1 v-if="configLoaded && isDeviceLinked" class="intro">
            <html-with-links tag="span" class="intro__inner" :html="$t('INSTALL_APPS_WITH_FREE_DEV_ACCOUNT')" />
        </h1>
        <div v-if="configLoaded" class="row">
            <div class="col col_1">
                <template v-if="isDeviceLinked">

                    <table class="table">
                        <template v-if="!config.free_account?.appleid || showConfigure">
                            <tr>
                                <td class="cell">
                                    Apple account
                                </td>
                                <td class="td">
                                    <input class="input" v-model="appleid" />
                                </td>
                            </tr>
                            <tr>
                                <td class="cell">
                                    {{ $t('PASSWORD') }}
                                </td>
                                <td class="td">
                                    <input class="input" type="password" v-model="password" />
                                </td>
                            </tr>
                            <tr>
                                <td class="cell">
                                </td>
                                <td class="td">
                                    <html-with-links tag="span" class="hint"
                                        :html="$t('PLUS_PRIVACY_HINT', { privacy_href: '/info/privacy' })" />
                                </td>
                            </tr>
                            <tr>
                                <td class="cell">
                                    {{ $t('SEND_SMS_TO_VERIFY_ACCOUNT') }}
                                </td>
                                <td class="td">
                                    <yes-no v-model="isSMSAuth"
                                        :options="[{ label: $t('YES'), value: 1 }, { label: $t('NO'), value: 0 }]"
                                        fit-content />
                                    <html-with-links tag="span" class="hint hint_mt" :html="$t('SMS_AUTH_HINT')" />
                                </td>
                            </tr>
                            <tr>
                                <td class="cell"></td>
                                <td class="td">
                                    <btn class="configure-button" type="primary" :loading="configuring"
                                        :disabled="!appleid || !password" @click.native="configureFreeAccount">
                                        {{ $t('CONFIGURE_FREE_ACCOUNT') }}
                                    </btn>
                                </td>
                            </tr>
                        </template>
                        <template v-if="config.free_account?.appleid && !showConfigure">
                            <tr>
                                <td class="cell">
                                    Apple account
                                </td>
                                <td class="td">
                                    {{ config.free_account?.appleid }}
                                    <br />
                                    <btn class="configure-button" type="primary" @click.native="reconfigureAccount">
                                        {{ $t('RECONFIGURE_YOUR_ACCOUNT') }}
                                    </btn>
                                </td>
                            </tr>
                            <tr>
                                <td class="cell"></td>
                                <td class="td">
                                    <btn class="delete-account-button" type="negative" :loading="deletingAccount"
                                        @click.native="deleteAccount">
                                        {{ $t('DELETE_FREE_DEV_ACCOUNT') }}
                                    </btn>
                                </td>
                            </tr>
                        </template>
                        <tr v-if="config.free_account?.status_translated">
                            <td class="cell">
                                {{ $t('STATUS') }}
                            </td>
                            <td class="td">
                                <span v-html="config.free_account?.status_translated"></span>
                                <template v-if="config.free_account?.status === 'otp_required' && !configuring">
                                    <br />
                                    <btn class="configure-button" type="primary" :loading="sendingOtp"
                                        @click.native="sendOtp">
                                        {{ $t('ENTER_OTP_SHORT') }}
                                    </btn>
                                </template>
                            </td>
                        </tr>
                        <tr
                            v-if="config.free_account?.reconfiguration_at && config.free_account.reconfiguration_at !== 0">
                            <td class="cell">
                                {{ $t('MANDATORY_RECONFIGURATION_DATE') }}
                            </td>
                            <td class="td">
                                {{ new Date(config.free_account.reconfiguration_at * 1000).toLocaleString() }}<br />
                                <span class="hint hint_mt">{{ $t('MANDATORY_RECONFIGURATION_NOTE') }}</span>
                            </td>
                        </tr>
                    </table>

                    <!-- Free Developer Account Capability Check -->
                    <div class="dev-account-check-container">
                        <important-message v-if="currentDevice.can_use_free_developer_account === 'yes'" type="info">
                            <div v-html="$t('CURRENT_DEVICE_CAN_USE_FREE_DEV_ACCOUNT_NOTICE')"></div>
                        </important-message>

                        <important-message v-else-if="currentDevice.can_use_free_developer_account === 'no'"
                            type="notice">
                            <div v-html="currentDevice.can_use_free_developer_account_validation_error.translated">
                            </div>
                        </important-message>
                    </div>
                </template>
            </div>
            <aside class="sidebar col col_2">
                <p class="sidebar__text g-text" v-html="$t('FREE_DEV_ACCOUNT_PAGE_SIDEBAR', { href: '/my/faq' })"></p>
            </aside>
        </div>
    </section>
</template>

<script>
import Loader from '@/components/Loader';
import Btn from '@/components/Button.vue';
import YesNo from '@/components/YesNo.vue';
import HtmlWithLinks from '@/components/HtmlWithLinks.vue';
import ImportantMessage from '@/components/ImportantMessage.vue';
import { mapGetters } from 'vuex';

export default {
    components: {
        Loader,
        Btn,
        HtmlWithLinks,
        YesNo,
        ImportantMessage,
    },
    data() {
        return {
            loading: false,
            configuring: false,
            sendingOtp: false,
            showConfigure: false,
            deletingAccount: false,
            appleid: '',
            password: '',
            isSMSAuth: 0,
        };
    },
    computed: {
        ...mapGetters(['devicesLoaded', 'isDeviceLinked']),
        title() {
            return this.$t('INSTALL_CONFIGURE_YOUR_FREE_DEV_ACCOUNT');
        },
        configLoaded() {
            return !!this.config.model || (this.devicesLoaded && !this.isDeviceLinked);
        },
        config() {
            return this.$store.getters.config;
        },
        currentDevice() {
            return this.$store.getters.devices
                .find(device => device.lt === this.$store.getters.lt) || {};
        },
    },
    methods: {
        async configureFreeAccount() {
            if (!this.$store.getters.canInstallApps) {
                await this.$alert(this.$t('TO_CONFIGURE_FREE_ACCOUNT_VISIT_FROM_DEVICE', { device: this.config.nice_idevice_model }));
                return;
            }

            this.configuring = true;
            const result = await this.$api.editFreeDevAccount({
                appleid: this.appleid,
                password: this.password,
                isSMSAuth: this.isSMSAuth,
                status: this.config.free_account?.appleid ? 'new' : '',
            })
                .catch(e => e);

            if (result instanceof Error) {
                this.configuring = false;
                return;
            }

            this.handleStatus(this.appleid)
                .finally(() => {
                    this.configuring = false;
                });
        },
        async handleStatus(appleid) {
            await Promise.all([
                this.$store.dispatch('getConfig'),
                this.$store.dispatch('getDevices')
            ]);
            const { status } = this.config.free_account;

            if (status === 'ok') {
                this.showConfigure = false;
                this.$notify({
                    type: 'success',
                    text: this.$t('FREE_ACCOUNT_CONFIGURED'),
                });
                return true;
            }

            if (status === 'error') {
                this.$notify({
                    type: 'error',
                    text: this.config.free_account.status_translated,
                });
                return false;
            }

            if (status === 'otp_required') {
                const otp = await this.$prompt(this.$t('ENTER_OTP'));

                // Validate OTP - ensure it's exactly 6 digits
                const trimmedOtp = otp.trim();
                if (!/^\d{6}$/.test(trimmedOtp)) {
                    this.$notify({
                        type: 'error',
                        text: this.$t('OTP_MUST_BE_6_DIGITS'),
                    });
                    // Try again
                    return this.handleStatus(appleid);
                }

                await this.$api.editFreeDevAccount({
                    appleid,
                    otp: trimmedOtp,
                });
            }

            return new Promise((resolve) => {
                setTimeout(() => {
                    resolve(this.handleStatus(appleid));
                }, 1000);
            });
        },
        deleteAccount() {
            this.$confirm(this.$t('DELETE_FREE_DEV_ACCOUNT_CONFIRMATION'))
                .then(() => {
                    this.deletingAccount = true;
                    return this.$api.deleteFreeDevAccount();
                })
                .then(() => Promise.all([
                    this.$store.dispatch('getConfig'),
                    this.$store.dispatch('getDevices')
                ]))
                .finally(() => {
                    this.deletingAccount = false;
                });
        },
        sendOtp() {
            this.sendingOtp = true;
            this.handleStatus(this.config.free_account.appleid)
                .finally(() => {
                    this.sendingOtp = false;
                });
        },
        reconfigureAccount() {
            if (!this.$store.getters.canInstallApps) {
                this.$alert(this.$t('TO_CONFIGURE_FREE_ACCOUNT_VISIT_FROM_DEVICE', { device: this.config.nice_idevice_model }));
                return;
            }

            if (this.config.free_account?.appleid && this.config.free_account.appleid.includes('@')) {
                this.appleid = this.config.free_account.appleid;
            }
            this.showConfigure = true;
        },
    },
    mounted() {
        const el = this.$route.hash && document.querySelector(this.$route.hash);
        if (el) {
            el.scrollIntoView();
        }
    },
    created() {
        // Check if account is in verifying status and start polling
        this.$store.dispatch('getConfig').then(() => {
            if (this.config.free_account) {
                if (this.config.free_account.status === 'otp_required') {
                    // Automatically trigger OTP entry if status is OTP required
                    this.sendOtp();
                } else if (this.config.free_account.status !== 'ok' &&
                    this.config.free_account.status !== 'error') {
                    // Start checking status if it's in a verifying state
                    this.handleStatus(this.config.free_account.appleid);
                }
            }
        });
    },
};
</script>

